<template>
    <div class="clientContent" :style="{width:layoutWidth+'px'}">
       <Row>
        <Col span="24">
            <div class="crm-tit">
                <h2 class="dt-tit">
                
                </h2>
                <Input
                    placeholder=""
                    icon="ios-search-strong"
                    class="fl dt-sc-box">
                </Input>
                <div class="fr dt-right">
                    <a href="/dist/createNewClient" class="ivu-btn ivu-btn-primary">
                      
                    </a>
                    <Dropdown placement="bottom-end"  @on-click="handelMore">
                        <Button>
                            
                            <Icon type="arrow-down-b"></Icon>
                        </Button>
                        <DropdownMenu slot="list">
                            <DropdownItem></DropdownItem>
                            <DropdownItem></DropdownItem>
                            <DropdownItem name="export"></DropdownItem>
                        </DropdownMenu>
                    </Dropdown>  
                </div>
            </div>
        </Col>
       </Row>
       <Row>
        <Col span="24">
            <ul class="batch-trem clearfix" v-show="checkFlag">
                <li>
                    <span class="item-tit fl"></span>
                    <Select v-model="bussinessTypeValue" style="width:150px">
                        <Option
                            v-for="item in bussinessType" 
                        :value="item.value" 
                        :key="item.value">
                        {{ item.label }}
                        </Option>
                    </Select>
                </li>
                <li>
                    <span class="item-tit fl"></span>
                    <Select v-model="sceneValue" style="width:150px">
                        <Option
                            v-for="item in scene" 
                        :value="item.value" 
                        :key="item.value">
                        {{ item.label }}
                        </Option>
                    </Select>
                </li>
                <li>
                    <span class="item-tit"></span>
                    <Button :class="[{'ivu-btn-primary':listMapFlag}, 'ivu-btn']" @click="listMapFlag = true"></Button>
                    <Button :class="[{'ivu-btn-primary':!listMapFlag}, 'ivu-btn']" @click="mapShow"></Button>
                </li>
                <li class="noborder" style="margin-left:10px">
                    <Button type="dashed"></Button>
                </li>
            </ul>
            <ul class="batch-trem clearfix" v-show="!checkFlag">
                <li>
                    <span class="item-tit item-sepcial fl"><i class="checkLen">{{selectLen}}</i></span>
                </li>
                <li class="noborder" style="margin-left:10px">
                    <Button @click="sendEmail"></Button>
                    <Button @click="transfer"></Button>
                    <Button @click="changePrincipal"></Button>
                    <Button @click="abolishFlag"></Button>
                    <Button @click="addGroupMemberFlag"></Button>
                    <Button @click="removeGroupMemberFlag"></Button>
                    <Button @click="lockFlag"></Button>
                    <Button @click="unlockFlag"></Button>
                </li>
            </ul>
        </Col>
       </Row>
       <Row>
        <table-column :selectFlag="tableColumnSelectFlag" :tableColumns="tableColumns" @tableHeadChange="tableHeadChange"></table-column>
        <Col v-show="listMapFlag" class="clearfix" span="24">
            <Col span="24">
                <div ref="mainList" id="mainList" :style="{height:mainListHeight +'px'}">
                    <div class="selectColumn" title="" @click="tableColumnSelectFlag=!tableColumnSelectFlag">
                        <Icon type="ios-gear" size="20" style="margin-top:10px;"></Icon>
                    </div> 
                    <Table :border="true" 
                            :stripe="true" 
                            :show-header="true" 
                            size="default" 
                            :height="mainListHeight"
                            :data="clientList" 
                            :columns="listHead"
                            :loading="tableLoadingFlag"
                            @on-selection-change="selectChange"
                            ref="tableClient"
                            @on-row-click="showInfo">
                    </Table>
                </div>                   
            </Col>
            <Col span="24">
                <div class="main-block">
                    <Page :total="clientTotalList.length" size="small" show-elevator show-sizer placement="top" @on-change="pageTurn" @on-page-size-change="pageSizeChange"></Page>
                </div>
            </Col>      
        </Col>
        <Col  span="24" v-show="!listMapFlag" ref="mapListShow" id="mapListShow">
            <client-list-map class="clientListMap fl" :clientList="clientList" ref="clientListMap" :h="layoutHeight - 116">
            </client-list-map>
            <div ref="listMap" id="listMap" :style="{height:listMapHeight+'px'}">
            </div>
        </Col>  
       </Row>
        <Row class="clientInfoMain" :style="{right:r+'px'}">
            <client-user-info @rightChange="rightChange" :selectItem="selectItem"></client-user-info>
        </Row>
   </div>  
</template>

<script>
import tableColumn from '@/components/tableColumn'
import clientListMap from '@/components/clientServer/clientListMap'
import ClientUserInfo from '@/components/clientServer/clientUserInfo'
import {deepClone} from '../../deepClone.js'
export default {
  components:{
      tableColumn,
      clientListMap,
      ClientUserInfo
  },
  data () {
      return {
        tableColumns:{  // 
            dutyOfficer:{
              columnName:'',
              selectFlag:false
            },
            dutyOfficerSection:{
               columnName:'',
               selectFlag:false
            },
             clientLevel:{
               columnName:'',
               selectFlag:true
            },
             clinetFrom:{
               columnName:'',
               selectFlag:true
            },
             detailLocation:{
               columnName:'',
               selectFlag:false
            },
             business:{
               columnName:'',
               selectFlag:true
            },
             createTime:{
               columnName:'',
               selectFlag:true
            },
             clientStatus:{
               columnName:'',
               selectFlag:true
            },
             clientId:{
               columnName:'',
               selectFlag:true
            },
             locationInfo:{
               columnName:'',
               selectFlag:true
            },
             phoneNum:{
               columnName:'',
               selectFlag:true
            },
             comment:{
               columnName:'',
               selectFlag:true
            },
             createUser:{
               columnName:'',
               selectFlag:true
            },
             moveNum:{
               columnName:'',
               selectFlag:false
            },
             modifyTime:{
               columnName:'',
               selectFlag:false
            },
             address:{
               columnName:'',
               selectFlag:true
            },
             group:{
               columnName:'',
               selectFlag:true
            },
              clientType:{
               columnName:'',
               selectFlag:false
            },
              fax:{
               columnName:'',
               selectFlag:true
            },
              url:{
               columnName:'',
               selectFlag:true
            },
              email:{
               columnName:'',
               selectFlag:true
            },
              lockStatus:{
               columnName:'',
               selectFlag:true
            },
            businessType:{
               columnName:'',
               selectFlag:true
            }
        },
        listMapFlag: true, // tab
        showCheckbox: true,  // tableCheckBox
        tableLoadingFlag:false, // 
        tableColumnSelectFlag:false,  //  true
        selectLen:0,      // 
        pageNum:1, // 
        pageSize:10,  // 
        r:-900, // 
        selectItem:{}, // table
        bussinessType: [  // 
            {
                value: '0',
                label: ''
            },
            {
                value: '1',
                label: ''
            }
        ],
        bussinessTypeValue: '0', // 
        scene: [ // 
          {
            value: '0',
            label: ''
          },
          {
            value: '1',
            label: ''
          },
          {
            value: '2',
            label: ''
          },
          {
            value: '3',
            label: ''
          },
          {
            value: '4',
            label: ''
          },
          {
            value: '5',
            label: ''
          }
        ],
        sceneValue: '1',  // 
        selectedClientList:[] // 
      }
  },
  watch:{
    // 
    clientTotalList:{
      deep:true,
      handler(){
         if(this.clientTotalList.length){
           this.clientList = this.clientTotalList.slice((this.pageNum-1)*this.pageSize,this.pageNum*this.pageSize)
         }
      }
    }
  },
  computed:{
    layoutWidth:{ // 
      get(){
         return this.$store.state.layoutWidth
      },
      set(value){

      }
    },
    layoutHeight:{ // 
      get(){
        return this.$store.state.layoutHeight
      },
      set(){

      }
    },
    checkFlag:{ // flag
      get(){
        return this.$store.state.checkFlag
      },
      set(value){
         this.$store.commit('setCheckFlag',value)
      }
    },
    mainListHeight:{ // table
      get(){
        return this.layoutHeight - 170
      },
      set(value){
      }
    },
    listMapHeight:{ // 
      get(){
          return this.layoutHeight - 116
      },
      set(value){
      }
    },
    listHead:{ //
      get(){
        return this.setTableHead()
      },
      set(value){
      }
    },
    clientTotalList:{ // 
      get(){
        return this.$store.state.clientList
      },
      set(){
      }
    },
    clientList:{  // 
      get(){
         if(this.clientTotalList.length){
           return this.clientTotalList.slice((this.pageNum-1)*this.pageSize,this.pageNum*this.pageSize)
         }else{
           return []
         }
      },
      set(value){

      }
    }
  },
  methods:{
    // 
    setTableHead (){
      let columns = []
      if (this.showCheckbox) { // checkBox
        columns.push({
          type: 'selection',
          width: 60,
          align: 'center'
        })
      }
      if (this.showIndex) { // index
        columns.push({
          type: 'index',
          width: 60,
          align: 'center',
          fixed: 'left'
        })
      }
      if(this.tableColumns.clientId.selectFlag){ //           
        columns.push({
          width: 150,
          title: '',
          key: 'clientId',
          sortable: true
        }) 
      }
      columns.push({ // 
        width: 120,
        title: '',
        key: 'clientName',
        sortable: true,
        ellipsis: true
      })
      if(this.tableColumns.dutyOfficer.selectFlag){ // 
        columns.push({
          width: 120,
          title: '',
          key: 'dutyOfficer',
          sortable: true,
          ellipsis: true
        })  
      }
      if(this.tableColumns.dutyOfficerSection.selectFlag){ // 
        columns.push({
          width: 150,
          title: '',
          key: 'dutyOfficerSection',
          sortable: true,
          ellipsis: true
        })
      }
      if(this.tableColumns.clientLevel.selectFlag){ // 
        columns.push({
          width: 120,
          title: '',
          key: 'clientLevel',
          sortable: true
        })
      }
      if(this.tableColumns.clinetFrom.selectFlag){ // 
        columns.push({
          width: 120,
          title: '',
          key: 'clinetFrom',
          sortable: true,
          ellipsis: true
        })
      }
      if(this.tableColumns.locationInfo.selectFlag){ // 
        columns.push({
          width: 200,
          title: '',
          key: 'detailLocation',
          sortable: true,
          ellipsis: true
        //   filters: [
        //     {
        //       label: '25',
        //       value: 1
        //     },
        //     {
        //       label: '25',
        //       value: 2
        //     }
        //   ],
        //   filterMultiple: false,
        //   filterMethod (value, row) {
        //     if (value === 1) {
        //       return row.age > 25
        //     } else if (value === 2) {
        //       return row.age < 25
        //     }
        //   }
        })
      }
      if(this.tableColumns.business.selectFlag){ // 
        columns.push({
          width: 120,
          title: '',
          sortable: true,
          key: 'business',
          ellipsis: true
          // filters: [
          //   {
          //     label: '',
          //     value: ''
          //   },
          //   {
          //     label: '',
          //     value: ''
          //   },
          //   {
          //     label: '',
          //     value: ''
          //   }
          // ],
          // filterMethod (value, row) {
          //   return row.address.indexOf(value) > -1
          // }
        })
      }
      if(this.tableColumns.createTime.selectFlag){ // 
        columns.push({
          width: 120,
          title: '',
          key: 'createTime',
          sortable: true,
          ellipsis: true
        })
      }
      if(this.tableColumns.clientStatus.selectFlag){ //      
        columns.push({
          width: 120,
          title: '',
          key: 'clientStatus',
          sortable: true
        })
      }
      if(this.tableColumns.locationInfo.selectFlag){ //   
        columns.push({
          width: 120,
          title: '',
          key: 'locationInfo',
          sortable: true,
          ellipsis: true
        })
      }
      if(this.tableColumns.phoneNum.selectFlag){ // 
        columns.push({
          width: 120,
          title: '',
          key: 'phoneNum',
          sortable: true,
          ellipsis: true
        })
      }
      if(this.tableColumns.comment.selectFlag){ //    
        columns.push({
          width: 120,
          title: '',
          key: 'comment',
          sortable: true,
          ellipsis: true
        })
      }
      if(this.tableColumns.createUser.selectFlag){ //    
        columns.push({
          width: 120,
          title: '',
          key: 'createUser',
          sortable: true,
          ellipsis: true
        })
      }
      if(this.tableColumns.moveNum.selectFlag){ //    
        columns.push({
          width: 120,
          title: '',
          key: 'moveNum',
          sortable: true
        })
      }
      if(this.tableColumns.modifyTime.selectFlag){ //   
        columns.push({
          width: 150,
          title: '',
          key: 'modifyTime',
          sortable: true,
          ellipsis: true
        })
      }
      if(this.tableColumns.group.selectFlag){ // 
        columns.push({
          width: 120,
          title: '',
          key: 'group',
          sortable: true,
          ellipsis: true
        })
      }
      if(this.tableColumns.clientType.selectFlag){ // 
        columns.push({
          width: 120,
          title: '',
          key: 'clientType',
          sortable: true
        })
      }
      if(this.tableColumns.fax.selectFlag){ // 
        columns.push({
          width: 120,
          title: '',
          key: 'fax',
          sortable: true,
          ellipsis: true
        })
      }
      if(this.tableColumns.url.selectFlag){ // 
        columns.push({
          width: 120,
          title: '',
          key: 'url',
          sortable: true,
          ellipsis: true
        })
      }
      if(this.tableColumns.email.selectFlag){ // 
        columns.push({
          width: 120,
          title: '',
          key: 'email',
          sortable: true,
          ellipsis: true
        })
      } 
      if(this.tableColumns.lockStatus.selectFlag){ // 
        columns.push({
          width: 120,
          title: '',
          key: 'lockStatus',
          sortable: true
        })
      }
      if(this.tableColumns.businessType.selectFlag){ // 
        columns.push({
          width: 120,
          title: '',
          key: 'businessType',
          sortable: true
        })
      }
      // columns.push({
      //   width: 120,
      //   key: 'action',
      //   align: 'center',
      //   fixed: 'right',
      //   renderHeader: (h, params) => {
      //     return h('div', [
      //       h('Icon', {
      //         props: {
      //           type: 'ios-gear',
      //         },
      //         on: {
      //           click: () => {
      //             this.show(params.index)
      //           }
      //         }
      //       }, ''),
      //       h('anchored-heading', {
      //         props: {
      //           level:1,
      //           items:[{name: 'test1'},{name: 'test2'}]
      //         },
      //         style:{
      //           position:'absolute',
      //           top:'0px',
      //           left:'0px'
      //         },
      //         on: {
      //           click: () => {
      //             this.remove(params.index)
      //           }
      //         }
      //       }, '')
      //     ],
      //     {
      //       style:{
      //         position:'relative'
      //       }
      //     })
      //   },
      //   render: (h, params) => {
      //     return h('div', [
      //       h('Button', {
      //         props: {
      //           type: 'text',
      //           size: 'small'
      //         },
      //         style: {
      //           marginRight: '5px'
      //         },
      //         on: {
      //           click: () => {
      //             this.show(params.index)
      //           }
      //         }
      //       }, ''),
      //       h('Button', {
      //         props: {
      //           type: 'text',
      //           size: 'small'
      //         },
      //         on: {
      //           click: () => {
      //             this.remove(params.index)
      //           }
      //         }
      //       }, '')
      //     ])
      //   }
      // })
      columns.push({
        width: 50,
        key: 'action',
        align: 'center',
        fixed: 'right',
        title:' ',
        // renderHeader: (h, params) => {
        //   return h('Icon', {
        //      props: {
        //         type: 'ios-gear',
        //         size:'18'
        //       },
        //       on: {
        //         click: () => {
        //         //  this.show(params.index)
        //         console.log(this)
        //         }
        //       }
        //   })
        // },
        render: (h, params) => {
          return h('div', [
            h('Button', {
              props: {
                type: 'text',
                size: 'small'
              },
              style: {
                marginRight: '5px'
              }
              // on: {
              //   click: () => {
              //     // this.show(params.index)
              //     console.log(this)
              //     this.r = 0
              //   }
              // }
            }, '')
          ])
        }
      })
        return columns
    },
    // 
    tableHeadChange (changeColumn, flag){
       this.tableColumns = deepClone(changeColumn)
       this.setTableHead()
       this.tableColumnSelectFlag = flag
    },
    // 
    selectChange (selection) {
      this.$store.commit('setSelectedDataList',selection)
      if(selection.length){
        this.$store.commit('setCheckFlag',false)
        this.selectLen = selection.length
      }else{
          this.$store.commit('setCheckFlag',true)
      }
    },
    // 
    mapShow () {
      this.listMapFlag = false
      setTimeout(function() {  //   display:block 200
        var newMap =  new BMap.Map('listMap')
        var point = new BMap.Point(116.331398, 39.897445)
        newMap.centerAndZoom(point, 12)
        var myGeo = new BMap.Geocoder()
        myGeo.getPoint("", function (point) {
          if (point) {
            newMap.centerAndZoom(point, 16)
            newMap.addOverlay(new BMap.Marker(point))
          } else {
            alert('!')
          }
        }, '')
        newMap.enableScrollWheelZoom(true)
      }, 200);
    },
    // 
    pageTurn (num) {
      this.pageNum = num
      this.$store.commit('setCheckFlag',true)
      this.$store.commit('setSelectedDataList',[])
    },
    // 
    pageSizeChange (size) {
       this.pageSize = size
       this.$store.commit('setCheckFlag',true)
       this.$store.commit('setSelectedDataList',[])
    },
    // 
    sendEmail () {
      this.$store.commit('setSendEmailFlag',true)
    },
    // 
    transfer () {
       this.$store.commit('setTransferFlag',true)
    },
    // 
    changePrincipal () {
       this.$store.commit('setChangePrincipalFlag',true)
    },
    // 
    abolishFlag (){
      this.$store.commit('setAbolishFlag',true)
    },
    // 
    addGroupMemberFlag(){
      this.$store.commit('setAddGroupMemberFlag',true)
    },
    // 
    removeGroupMemberFlag(){
      this.$store.commit('setRemoveGroupMemberFlag',true)
    },
    // 
    lockFlag (){
       this.$store.commit('setLockFlag',true)
    },
    // 
    unlockFlag (){
      this.$store.commit('setUnlockFlag',true)
    },
    // 
    rightChange (r){
       this.r = r
    },
    // 
    showInfo(item,index){
      this.r = 0
      this.selectItem = item
    },
    // 
    handelMore(name){
     if (name === 'export') {
        this.$router.push('/')
      }
    }
  }
}
</script>

<style>  
 .clientContent{
     height: 100%;
     overflow: hidden;
     position: relative;
 }
 .crm-tit{
    height: 60px;
    background-color: #f4f6f9;
    border-color: #d8dde8;
    position: riative;
    z-index: 2;
  }
  .dt-tit{
    float: left;
    padding: 0 20px;
    font-size: 18px;
    line-height: 60px;
  }
  .dt-sc-box{
    width: 260px;
    margin: 13px 0 0 300px;
  }
  .dt-right{
      margin:13px 50px 0 0;
      font-size: 14px;
  }
  #mainList{
    overflow: auto;
    position: relative;
  }
  #mainList .selectColumn{
    width: 50px;
    height: 40px;
    position: absolute;
    right: 0px;
    top:1px;
    z-index: 3;
    padding-left: 10px;
  }
  .main-block{
    background: #f4f6f9;
    padding: 15px;
  } 
  .clientListMap{
    overflow: auto;
    width: 300px;
  }
  #ListMap{
    height: 100%;
    float: left;
  }
  .clientInfoMain{
    width: 900px;
    height: 100%;
    position:absolute;
    top:0px;
    right:0px;
    background: #fff;
    z-index:9999;
    transition: .3s linear;
  }
  .batch-trem{
    padding: 9px 0 10px;
    border-top: 1px solid #d8dde8;
    background-color: #f4f6f9;
    line-height: 36px;
  }
  .batch-trem>li{
      float: left;
      padding-right: 20px;
      border-right: 1px solid #d8dde8;
  }
  .batch-trem .noborder{
      border:0;
  }
  .batch-trem .item-sepcial{
    font-size: 12px;
  }
  .batch-trem .checkLen{
    color: #2d8cf0;
    display: inline-block;
    padding: 0 5px;
  }

  .batch-trem .item-tit{
    padding-right: 20px;
    padding-left: 20px;
    color: #999;
    font-size: 14px
  }
</style>



