<template>
  <Row>
      <Col span="24">
        <div class="Tabtitle">
            <h3></h3>
        </div>
      </Col>
      <Col span="24" class="tabInfo">
        <Collapse value="1">
            <Panel name="1">
                
                <p slot="content" class="clearfix">
                    <ul class="clearfix">
                        <li>
                            <div>
                                <span class="tit"></span>
                               <i class="con">{{selectItem.clientName}}</i>
                            </div>
                        </li>
                         <li>
                            <div>
                                <span class="tit"></span>
                               <i class="con">{{selectItem.clientType}}</i>
                            </div>
                        </li>
                         <li>
                            <div>
                                <span class="tit"></span>
                               <i class="con">{{selectItem.clinetFrom}}</i>
                            </div>
                        </li>
                         <li>
                            <div>
                                <span class="tit">1</span>
                               <i class="con">{{selectItem.businessLevel1}}</i>
                            </div>
                        </li>
                         <li>
                            <div>
                                <span class="tit">2</span>
                               <i class="con">{{selectItem.businessLevel2}}</i>
                            </div>
                        </li>
                        <li>
                            <div>
                                <span class="tit"></span>
                               <i class="con">{{selectItem.phoneNum}}</i>
                            </div>
                        </li>
                        <li>
                            <div>
                                <span class="tit"></span>
                               <i class="con">{{selectItem.fax}}</i>
                            </div>
                        </li>
                        <li>
                            <div>
                                <span class="tit"></span>
                               <i class="con">{{selectItem.email}}</i>
                            </div>
                        </li>
                        <li>
                            <div>
                                <span class="tit"></span>
                               <a class="con url" :href="selectItem.url">{{selectItem.url}}</a>
                            </div>
                        </li>
                        <li>
                            <div>
                                <span class="tit"></span>
                               <i class="con">{{selectItem.comment}}</i>
                            </div>
                        </li>
                        <li>
                            <div>
                                <span class="tit"></span>
                               <i class="con">{{selectItem.clientStatus}}</i>
                            </div>
                        </li>
                         <li>
                            <div>
                                <span class="tit"></span>
                               <i class="con">{{selectItem.lockStatus}}</i>
                            </div>
                        </li>
                    </ul>
                </p>
            </Panel>
            <Panel name="2">
                 
                <p slot="content">
                     <ul class="clearfix">
                        <li>
                            <div>
                                <span class="tit"></span>
                               <a class="con url" :href="'/dist/mapExpand?location='+encodeURIComponent(selectItem.locationInfo)" target="_blank">{{selectItem.locationInfo}}</a>
                            </div>
                        </li>
                         <li>
                            <div>
                                <span class="tit"></span>
                               <i class="con">{{selectItem.country}}</i>
                            </div>
                        </li>
                         <li>
                            <div>
                                <span class="tit"></span>
                               <i class="con">{{selectItem.province}}</i>
                            </div>
                        </li>
                         <li>
                            <div>
                                <span class="tit"></span>
                               <i class="con">{{selectItem.city}}</i>
                            </div>
                        </li>
                         <li>
                            <div>
                                <span class="tit"></span>
                               <i class="con">{{selectItem.area}}</i>
                            </div>
                        </li>
                        <li>
                            <div>
                                <span class="tit"></span>
                               <i class="con">{{selectItem.address}}</i>
                            </div>
                        </li>
                    </ul>
                </p>
            </Panel>
            <Panel name="3">
                
                <p slot="content">
                    <ul class="clearfix">
                        <li>
                            <div>
                                <span class="tit"></span>
                               <i class="con">{{selectItem.moveNum}}</i>
                            </div>
                        </li>
                         <li>
                            <div>
                                <span class="tit"></span>
                               <i class="con">90%</i>
                            </div>
                        </li>
                         <li>
                            <div>
                                <span class="tit"></span>
                               <i class="con">{{selectItem.dutyOfficerSection}}</i>
                            </div>
                        </li>
                         <li>
                            <div>
                                <span class="tit"></span>
                               <i class="con">{{selectItem.createTime}}</i>
                            </div>
                        </li>
                         <li>
                            <div>
                                <span class="tit"></span>
                               <i class="con">{{selectItem.modifyTime}}</i>
                            </div>
                        </li>
                        <li>
                            <div>
                                <span class="tit"></span>
                               <i class="con">{{selectItem.createUser}}</i>
                            </div>
                        </li>
                        <li>
                            <div>
                                <span class="tit"></span>
                               <i class="con">{{selectItem.createUser}}</i>
                            </div>
                        </li>
                    </ul>
                </p>
            </Panel>
        </Collapse>
      </Col>
  </Row>
</template>

<script>
export default {
  props:['selectItem']
}
</script>

<style>
  .Tabtitle{
      height: 52px;
      padding-top: 18px;
      box-sizing: border-box;
      padding-left: 16px;
  }
  .Tabtitle h3{
      line-height: 16px;
      padding-left: 8px;
      border-left:3px solid #12d4d7;
  }
  .tabInfo{
      padding-left: 16px;
  }
  .tabInfo .ivu-collapse{
      background: #fff;
      border: 0
  }
   .tabInfo .ivu-collapse>.ivu-collapse-item{
       border: 0;
   }
   .tabInfo ul li{
    width: 50%;
    float: left;
   }
   .tabInfo ul li div{
    margin-right: 10px;
    border-bottom: 1px solid #f2f2f2;
    padding-top: 14px;
    padding-bottom: 4px;
   }
   .tabInfo ul li .tit{
       color: #92a3b7;
       width: 130px;
       word-break: break-all;
       line-height: 24px;
       float: left;
   }
   .tabInfo ul li .con{
        line-height: 24px;
        white-space:nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        color: #444;
   }
   .tabInfo ul a.url{
       color:#57a3f3;
   }
   .tabInfo ul a.url:hover{
       text-decoration: underline;
   }
   .tabInfo .ivu-collapse-content>.ivu-collapse-content-box{
       padding-top: 0px;
   }
   .tabInfo .ivu-collapse>.ivu-collapse-item>.ivu-collapse-header{
       padding-left: 16px;
   }
</style>



